.header {
  overflow: hidden;
}

.header .background_image {
  z-index: -1;
  position: absolute;
  left: 0px;
  bottom: -30vh;
  object-fit: cover;
  width: 100%;
  height: 100vh;
  /* opacity: 0.9; */
}

.image {
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateY(-50%);
  transform: translateX(-50%);
  height: 15rem;
  border-radius: 100%;
  display: block;
}

.presentation_text {
  position: relative;
  top: 35%;
  color: rgb(255, 255, 255);
  text-shadow: 0 0 1rem rgb(0, 0, 0);
  font-size: 2.5rem;
}

.presentation_text strong {
  font-size: 3.5rem;
}

.links-list {
  list-style: none;
  position: absolute;
  bottom: 100px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-wrap: nowrap;
}

.links-list li {
  float: left;
}

.links-list li {
  margin-left: 2rem;
  margin-right: 2rem;
  font-size: 2.5rem;
}

.links-list.li,
a:link,
a:visited,
a:hover,
a:active {
  text-decoration: none;
  color: white;
}
